
.outer-wrapper {
    display: flex;
    justify-content: center;
    width: var(--mid-container-width);
}

.nav-wrapper {
    background-color: #017687;
}

.nav-container {
    z-index: 100;
    display: flex;
    align-content: center;
    align-items: center;
}

.nav-container a,
.nav-container span {
    padding: 10px 15px 10px 0;
    text-decoration: none;
    color: white;
    text-shadow: 1px 1px 1px #333333;
}

.nav-container a:hover,
.nav-container span:hover {
    font-weight: bold;
}

.nav-container a:first-child {
    font-size: 2rem;
}

.nav-container span {
    cursor: pointer;
    display: inline-block;
}

.active-link {
    font-weight: bold;
    color: var(--primary);
}

.login-btn {
    margin-left: auto;
    color: white;
    text-shadow: 1px 1px 1px #333333;
}

.login-btn span {
    padding-right: 0;
    padding-left: 15px;
}

/* Mobile */

@media (max-width: 576px) {}

/* Large devices (desktops, 992px and up) */

@media (min-width: 992px) {}
.new-event-form,
.new-greeting-form {
    display: grid;
    grid-template-columns: 100px 1fr;
    grid-gap: 10px;
}

.guest-picker {
    display: grid;
    grid-template-columns: 1fr;
    grid-row-gap: 5px;
}

.guest-input {
    display: grid;
    grid-template-columns: 5rem 1fr;
    grid-gap: 5px;
}

.icon-button {
    width: 2rem;
}

input,
.form select{
    padding: 5px;
}


/* Mobile */

@media (max-width: 576px) {
    .new-event-form {
        grid-template-columns: 1fr;
    }
    .guest-input {
        display: grid;
        grid-template-columns: 1fr;
    }
}

/* Large devices (desktops, 992px and up) */

@media (min-width: 992px) {}
.greeting-tabs-container {
    display: flex;
    width: 100%;
    background-color: var(--primary-light);
}

.greeting-tab {
    color: white;
    padding: 10px;
    cursor: pointer;
}

.greeting-tab:hover {
    background-color: var(--primary);
}

.greeting-tab-pill {
    font-size: 0.8rem;
    border-radius: 50%;
    background-color: var(--secondary);
    width: 20px;
    height: 20px;
    display: inline-block;
    text-align: center;
    font-weight: bold;
    color: black;
}

.active-tab {
    background-color: var(--primary-dark);
}

.greeting-wrapper {
    border-bottom: 1px solid gray;
    display: grid;
    grid-template-columns: 75% 1fr;
    grid-gap: 10px;
}

.greeting-wrapper .buttons {
    display: flex;
}

.greeting-wrapper .buttons span {
    cursor: pointer;
    color: var(--primary-light);
    margin: 5px;
    -webkit-filter: drop-shadow(1px 1px 2px #666666);
            filter: drop-shadow(1px 1px 2px #666666);
    /* text-shadow: 2px 2px 2px #333333; */
}

.greeting-wrapper .readonly * {
    padding: 0;
    margin: 0;
}

/* Mobile */

@media (max-width: 576px) {
    .greeting-wrapper {
        grid-template-columns: 1fr;
    }
}
.banner {
    background-size: cover;
    width: 100%;
    height: 0;
    padding-bottom: 100%;
}

.main-body {
    background-color: rgba(255, 255, 255, 0.7);
    padding: 10px;
    border-radius: 10px;
}

.home {
    background-image: url(/static/media/main_bg-min.74110a46.jpg);
}

.events {
    background-image: url(/static/media/events-bg.8826636b.jpg);
}

.about {
    background-image: url(/static/media/about-bg.2862b313.jpg);
}

.party {
    background-image: url(/static/media/party-bg.c31c6e18.jpg);
}

.greeting {
    background-image: url(/static/media/gr-bg.d934c050.jpg);
}

.details {
    background-image: url(/static/media/details-bg.af203670.jpg);
}

.hero {
    color: white;
    height: 200px;
    display: flex;
    align-items: center;
}

.hero .title {
    font-size: 4rem;
    font-weight: bold;
    text-shadow: 2px 2px 2px #333333;
}

.hero .description {
    font-size: 0.9rem;
    text-shadow: 1px 1px 1px #333333;
}

.btn {
    padding: 10px 20px;
    margin: 10px 0;
    border: none;
    background-color: blueviolet;
    color: white;
    border-radius: 20px;
    box-shadow: 3px 6px 3px -3px #666666;
}

.loader-container {
    margin: 10px 0;
}

h3 {
    color: blueviolet;
}

.user-events {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 5px;
}

.user-events>div {
    padding: 3px;
    display: grid;
    grid-gap: 15px;
    grid-template-columns: 200px 150px repeat(3, minmax(-webkit-min-content, -webkit-max-content));
    grid-template-columns: 200px 150px repeat(3, minmax(min-content, max-content));
}

.event-datails {
    display: grid;
    grid-template-columns: 100px 1fr;
    grid-gap: 10px;
}

.event-datails ul {
    padding: 0;
    padding-left: 20px;
    margin: 0;
}
.chat-screen-container {
    height: 300px;
    display: flex;
    flex-direction: column;
    border: 1px #2c303b solid;
}

.chat-container {
    display: flex;
    flex: 1 1;
}

.whos-online-list-container {
    width: 150px;
    flex: none;
    background-color: #2c303b;
    color: white;
    overflow-y: scroll;
}

.chat-list-container {
    background-color: white;
    width: 85%;
    display: flex;
    flex-direction: column;
}

.message-list-container {
    overflow-y: scroll;
    flex: 1 1;
}

.message-list-container ul {
    list-style: none;
    padding: 0;
    margin: 10px
}

.message-list-container ul li {
    margin-top: 5px;
    margin-bottom: 5px;
}

.sender-username {
    font-weight: bold;
}

.message {
    font-size: 0.9rem;
    margin-bottom: 0.5rem;
}

.send-container form {
    display: flex;
}

.send-container form input {
    color: inherit;
    background: none;
    outline: none;
    border-top: 1px #2c303b solid;
    flex: 1 1;
    font-size: 16px;
}

.whos-online-list {
    padding: 0;
    margin: 10px;
    font-size: 0.9rem;
}

.whos-online-list li {
    display: grid;
    grid-template-columns: 15px 1fr;
    grid-gap: 10px;
    margin: 10px 0;
}

.whos-online-list li div {
    display: flex;
    align-items: center;
}

.whos-online-pill {
    border-radius: 50%;
    width: 11px;
    height: 11px;
}
:root {
    /* colors */
    --page-bg: #f9fafe;
    --text-color: #3a416f;
    --primary-dark: #6d1b7b;
    --primary: #9c27b0;
    --primary-light: #af52bf;
    --secondary-dark: #b28900;
    --secondary: #ffc400;
    --secondary-light: #ffcf33;
    /* dimensions */
    --min-container-width: 95%;
    --mid-container-width: 75%;
    --max-container-width: 75%;
    /* fonts */
    --body-font: 'Open Sans', sans-serif;
}

@font-face {
    font-family: 'Open Sans';
    src: local('OpenSans'), url(/static/media/OpenSans-Regular.d7d5d458.ttf) format('truetype');
}

@font-face {
    font-family: 'Open Sans';
    src: local('OpenSans'), url(/static/media/OpenSans-Bold.f5331cb6.ttf) format('truetype');
    font-weight: bold;
}
html {
    height: 100%
}
body,
html {
    margin: 0;
    padding: 0;
    font-family: 'Open Sans', sans-serif;
    font-family: var(--body-font);
    background-color: #f9fafe;
    background-color: var(--page-bg);
}

button {
    font-family: 'Open Sans', sans-serif;
    font-family: var(--body-font);
    cursor: pointer;
}

.outer-wrapper {
    width: 100%;
}

.container {
    width: 75%;
    width: var(--mid-container-width);
}
/* Mobile */

@media (max-width: 576px) {
    .container {
        width: 95%;
        width: var(--min-container-width);
    }
}

/* Large devices (desktops, 992px and up) */

@media (min-width: 992px) {
    .container {
        width: 75%;
        width: var(--max-container-width);
    }
}
